<template>
  <div class="card" style="flex-basis:20%; flex-grow: 0; margin-left: 15px;">
    <router-link class="card-link" :to="'/events/details/' + event.Id">
      <img class="card-img-top" :src="require('../../assets/' + pictureName)" style="width:100%">
      <div class="card-block">
        <h4 class="card-title">{{event.Name}}</h4>
        <p class="card-text">
          <i class="material-icons" style="vertical-align:middle">date_range</i>
          {{event.StartDate | formatdate}} to {{event.EndDate | formatdate}}
        </p>
        <p class="card-text">
          <i class="material-icons" style="vertical-align:middle">place</i>{{event.Place}}</p>
        <p v-if="applicationopen" class="card-text">
          <i class="material-icons" style="vertical-align:middle">check</i> Application open</p>
      </div>
      <div class="card-footer">
        <img class="card-img-top" src="../../assets/times.png">
      </div>
    </router-link>
  </div>
</template>

<script>
import moment from 'moment';
import eventsmenu from './eventsmenu.vue';
import eventinfo from './eventinfo.vue';
import eventtravel from './eventtravel.vue';
export default {
  data() {
    return {
      isSelected: false,
    }
  },
  props: {
    event
  },
  components: {
    eventsmenu,
    eventinfo,
    eventtravel
  },
  filters: {
    formatdate(value) {
      if (value) {
        return moment(value).format('DD.MM.YYYY');
      }
    }
  },
  computed: {
    // Make this return true or false and have apply now button depending on that.
    applicationopen: function() {
      let now = moment();
      if (now.isBefore(this.event.ApplicationEndDate)) {
        return true;
      }
      else {
        return false;
      }
    },
    pictureName: function() {
      let rand = Math.random();
      if (rand < 0.25) {
        return 'oslo.jpg';
      }
      else if (rand < 0.5) {
        return 'istanbul.jpg';
      }
      else if (rand < 0.75) {
        return 'stockholm.jpg';
      }
      else {
        return 'berlin.jpg';
      }
    },
  },
}
</script>

<style>
.event-details {
  display: none;
}

.event-details-name {
  color: #205E44;
  padding-top: 5px;
  padding-left: 5px;
  padding-right: 5px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.event-details-place {
  padding-top: 5px;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 0.8em;
}

.event-details-dates {
  padding-bottom: 5px;
  margin-left: 5px;
  padding-right: 5px;
  color: black;
  text-decoration: none !important;
  text-align: center;
  font-size: 0.8em;
}

.event-details-application {
  padding-bottom: 5px;
  margin-left: 5px;
  padding-right: 5px;
  color: black;
  text-decoration: none !important;
  text-align: center;
  font-size: 0.8em;
}

.event-box {
  background-color: #E8EAEA;
  border: 1px solid #bccec7;
  font-weight: bold;
  font-size: medium;
  margin-bottom: 5px;
  position: relative;
}

.event-star-button {
  right: 0;
  top: 0;
  position: absolute;
  border-color: black;
}

.selected {
  color: #E75480 !important;
}

.event-apply {
  right: 30px;
  top: 0px;
  position: absolute;
  color: #205E44;
}

@media (max-width: 600px) {
  .event-details-name {
    width: 200px;
    display: block;
    white-space: nowrap;
  }
}

#description img {
  max-width: 100%;
  width: 100% !important;
  height: auto !important;
}

#description {
  font-size: 16px !important;
}
</style>


